<template>
  <div id="app">
    <router-view/>
  </div>
</template>

<script>
export default {
  name: 'App'
}
</script>

<style>
.flex-grow::-webkit-scrollbar {display:none}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.page {
    height: 100%;
    width: 100%;
    display: flex;
    box-sizing: border-box;
    flex-direction: column;
  }
.box {
    flex: 1;
    width: 100%;
    box-sizing: border-box;
    padding: 1rem;
    overflow: hidden;
  }
  .box .content-item {
    width: 50%;
    box-sizing: border-box;
    padding: 0px 10px;
    display: flex;
    flex-direction: column;
    position: relative;
  }
  .flex-grow {
    height: 100%;
    width: 100%;
    box-sizing: border-box;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: flex-start;
    align-content: flex-start;
    align-items: flex-start;
    color: #fff;
    overflow-x: hidden;
    overflow-y: auto !important;
  }
  @media (max-width: 414px) {
    .box .content-item {
      width: 100% !important;
    }
  }
</style>



