import Vue from 'vue'
import Router from 'vue-router'


Vue.use(Router)

export default new Router({
  linkActiveClass:'active',
  routes: [
    {
      path: '/',
      name: 'login',
      component: () => import('@/components/login'),
    },
    {
      path: '/index',
      name: 'index',
      component: () => import('@/components/index'),
      redirect: '/about_me',
      children: [
        {
          path: '/about_me',
          name: 'about_me',
          component: () => import('@/components/about_me'),
        },
        {
          path: '/skill',
          name: 'skill',
          component: () => import('@/components/skill'),
        },
        {
          path: '/work_experience',
          name: 'work_experience',
          component: () => import('@/components/work_experience'),
        },
        {
          path: '/contact_me',
          name: 'contact_me',
          component: () => import('@/components/contact_me'),
        },
      ]
    }

  ],
})



